.wait-turn{

    display: block;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(189, 195, 199, 0.3);
    z-index: 1000;
}

.wait-turn p{
    font-size: 70px;
    display: block;
    text-align: center;
    margin-top: 24%;
}

@media screen and (max-height: 1440px){
    
    .loading{
        text-align: center;
        font-size: 100px;
        color: #F0A60C;
        margin: 500px auto;
    }
    .card-container{
        display: grid;
        grid-template-columns: 20% 20% 20% 20% 20%;
        padding: 80px;
    }
    .card{
        text-align: center;
        background-image: linear-gradient(to left, #6D1576 , #330066);
        color: #D0B7F9;
        font-size: 50px;
        margin: 8px;
        padding: 66px 20px;
        border-radius: 8px;
        cursor: pointer;
        z-index: 10;
    }
    .category{
        color: #F0A60C !important;
        cursor: default !important;
        font-size: 40px !important;
    }
    .no-render{
        cursor: default;
    }
    .top-text{
        color: #D0B7F9;
        text-align: center;
        padding: 30px 0;
        font-size: 50px;
    }
    .back-home{
        text-decoration: none;
        color: #D0B7F9;
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 1002;
    }

}

@media screen and (max-height: 1080px) {
    
    .loading{
        text-align: center;
        font-size: 100px;
        color: #F0A60C;
        margin: 280px auto;
    }
    .card-container{
        display: grid;
        grid-template-columns: 20% 20% 20% 20% 20%;
