        padding: 35px 20px;
        border-radius: 8px;
        cursor: pointer;
        z-index: 10;
    }
    .category{
        color: #F0A60C !important;
        cursor: default !important;
        font-size: 25px !important;
    }
    .no-render{
        cursor: default;
    }
    .top-text{
        color: #D0B7F9;
        text-align: center;
        padding: 30px 0;
        font-size: 50px;
    }
    .back-home{
        text-decoration: none;
        color: #D0B7F9;
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 1002;;
    }

}